.sidebar {
    width: 250px;
    height: 100vh;
    background-color: #fff;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center; /* Center content horizontally */
    padding-top: 20px;
  }
  
  .logo {
    font-size: 54px;
    font-style:italic;
    color: #ef07f7;
    margin-bottom: 30px;
    text-align: center;
  }
  
  .user {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center content horizontally */
    margin-bottom: 40px;
  }
  
  .user-image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 10px;
  }
  
  .user span {
    font-size: 18px;
    color: #555;
  }
  
  .menu {
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .menu-item {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    color: #555;
    transition: background 0.2s ease;
  }
  
  .menu-item:hover {
    background-color: #f0f0f0;
  }
  
  .menu-item.active {
    background-color: #ef28f9;
    font-weight: bold;
    color: white;
  }
  
  .menu-icon {
    margin-right: 10px;
    font-size: 18px;
  }
  
  .menu-item:nth-child(1) .menu-icon {
    color: #3498db; /* Blue for My Cards */
}

.menu-item:nth-child(2) .menu-icon {
    color: #2ecc71; /* Green for Expenses */
}

.menu-item:nth-child(3) .menu-icon {
    color: #f39c12; /* Orange for Wallet */
}

.menu-item:nth-child(4) .menu-icon {
    color: #9b59b6; /* Purple for Manage Team */
}

.menu-item:nth-child(5) .menu-icon {
    color: #e74c3c; /* Red for Manage Cards */
}

.menu-item:nth-child(6) .menu-icon {
    color: #34495e; /* Dark Blue for Company Profile */
}

.menu-item:nth-child(7) .menu-icon {
    color: #1abc9c; /* Teal for Manage Categories */
}

.menu-item:nth-child(8) .menu-icon {
    color: #e67e22; /* Orange for My Profile */
}
