.hero-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 30px;
    background-color: #f8f9fa;
    border-bottom: 1px solid #eaeaea;
  }
  
  /* Left Section */
  .hero-left h1 {
    font-size: 24px;
    font-weight: 600;
    margin: 0;
    color: #333;
  }
  
  .hero-left p {
    font-size: 16px;
    color: #555;
    margin: 5px 0 0;
  }
  
  .hero-left strong {
    color: #333;
    font-weight: 700;
  }
  
  /* Centered Search Bar */
  .hero-center {
    flex: 1;
    display: flex;
    justify-content: center;
  }
  
  .search-bar {
    display: flex;
    align-items: center;
    /* border: 1px solid #ddd; */
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #fff;
    width: 300px;
  }
  
  .search-icon {
    font-size: 16px;
    color: #999;
    margin-right: 5px;
  }
  
  .search-bar input {
    border: none;
    outline: none;
    font-size: 10px;
    color: #333;
    width: 100%;
  }
  
  /* Right Section */
  .hero-right {
    display: flex;
    gap: 20px;
  }
  
  .filters {
    display: flex;
    gap: 20px;
  }
  
  .filter-item {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  .filter-icon {
    font-size: 18px;
    color: #555;
    cursor: pointer;
    transition: color 0.2s ease;
  }
  
  .filter-icon:hover {
    color: #000;
  }
  
  .filter-item span {
    font-size: 14px;
    color: #555;
  }
  