.expense-card {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    transition: transform 0.2s ease;
    min-height: 300px; /* Adjust this value to set the desired height */
}

.expense-card:hover {
    transform: scale(1.02);
}

/* Header Section */
.card-header {
    display: flex;
    align-items: center;
    gap: 15px;
}

.profile-image img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
    flex-shrink: 0;
}

.header-details h3 {
     
    font-size: 1.2rem;
    font-weight: bold;
    
}

.header-details .user-name {
    color: #555;
    font-size: 0.9rem;
 
}

.meta-details {
    display: flex;
    gap: 100px;
    font-size: 0.85rem;
    color: #888;
}

.meta-details .time,
.meta-details .date {
    display: flex;
    align-items: center;
    gap: 5px;
}

/* Body Section */
.card-body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
}

.detail-row {
    display: contents;
}

.detail-item {
    background: #f9f9f9;
    padding: 10px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 0.9rem;
}

.detail-item strong {
    margin-right: 5px;
}
