.expense-card {
    background-color: #fff;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .card-header {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 80px;
  }
  
  .user-name {
    color: #888;
    font-size: 14px;
     
  }
  
  .card-meta {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    color: #777;
    margin-bottom: 100px;
  }
  
  .card-details {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  
  .category {
    color: #555;
    font-size: 14px;
  }
  
  .amount {
    font-weight: bold;
    color: #00a000;
  }
  
  .note {
    color: #555;
    font-size: 14px;
  }
  
  .receipt {
    color: #007bff;
    cursor: pointer;
  }
  .expenses-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Adjusts cards in rows */
    gap: 20px;  
    padding: 20px;
  }
  
  @media (max-width: 768px) {
    .expenses-grid {
      grid-template-columns: 1fr; /* Stacks cards in smaller screens */
    }
  }
  
  